@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: "Rubik", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

.header {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: hsl(229, 31%, 21%);
}

h1,
h2,
h3,
h4 {
  @apply text-slate-800 font-bold;
}

p {
  @apply text-slate-600;
  line-height: 1.8;
}

button {
  @apply text-slate-600;
}

.bg-gray {
  background-color: hsl(229, 31%, 21%);
}

.btn-blue {
  background-color: hsl(231, 69%, 60%);
}
.bg {
  position: absolute;
  right: -40%;
  bottom: 0;
  height: 150px;
  width: 100%;
  background-color: hsl(231, 69%, 60%);
  border-radius: 200px 0 0 200px;
  z-index: -1;
}

.bg-left {
  position: absolute;
  right: 20%;
  top: 25%;
  bottom: 0;
  height: 150px;
  width: 100%;
  background-color: hsl(231, 69%, 60%);
  border-radius: 0 200px 200px 0;
  z-index: -1;
}

@media (min-width: 1024px) {
  body {
    overflow-x: hidden;
  }

  .bg {
    position: absolute;
    right: -40%;
    bottom: 0;
    height: 250px;
    width: 100%;
    background-color: hsl(231, 69%, 60%);
    border-radius: 200px 0 0 200px;
    z-index: -1;
  }

  .bg-left {
    position: absolute;
    right: 30%;
    top: 50%;
    bottom: 0;
    height: 250px;
    width: 100%;
    background-color: hsl(231, 69%, 60%);
    border-radius: 0 200px 200px 0;
    z-index: -1;
  }
}
